.columns {
  margin: 1em -1em;
  min-width: 100%;
  display: flex; }
  .columns.columns-liquid {
    display: inherit;
    margin-right: 0;
    margin-left: 0; }
  .columns.columns-1 .column {
    width: 100%; }
  .columns.columns-1.columns-liquid {
    column-count: 1;
    column-gap: 2em; }
  .columns.columns-2 .column {
    width: 50%; }
  .columns.columns-2.columns-liquid {
    column-count: 2;
    column-gap: 2em; }
  .columns.columns-3 .column {
    width: 33.33333%; }
  .columns.columns-3.columns-liquid {
    column-count: 3;
    column-gap: 2em; }
  .columns.columns-4 .column {
    width: 25%; }
  .columns.columns-4.columns-liquid {
    column-count: 4;
    column-gap: 2em; }
  .columns.columns-5 .column {
    width: 20%; }
  .columns.columns-5.columns-liquid {
    column-count: 5;
    column-gap: 2em; }
  .columns.columns-6 .column {
    width: 16.66667%; }
  .columns.columns-6.columns-liquid {
    column-count: 6;
    column-gap: 2em; }
  .columns.columns-7 .column {
    width: 14.28571%; }
  .columns.columns-7.columns-liquid {
    column-count: 7;
    column-gap: 2em; }
  .columns.columns-8 .column {
    width: 12.5%; }
  .columns.columns-8.columns-liquid {
    column-count: 8;
    column-gap: 2em; }
  .columns.columns-9 .column {
    width: 11.11111%; }
  .columns.columns-9.columns-liquid {
    column-count: 9;
    column-gap: 2em; }
  .columns:not(.columns-liquid) > .column {
    box-sizing: border-box;
    padding: 0 1em; }
    .columns:not(.columns-liquid) > .column.column-narrow {
      width: 33.33333%; }
    .columns:not(.columns-liquid) > .column.column-wide {
      width: 66.66666%; }
  .columns:after {
    content: "";
    display: table;
    clear: both; }
